.dialogs {
    display: grid;
    grid-template-areas:
     "d n"
     "d m";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    width: 100%;
}

.dialog {

}

.dialogItems {
    padding: 10px;
    grid-area: d;
}

.dialogItems .active {
    color: goldenrod;
}

.messages {
    padding: 10px;
    grid-area: m;
}

.messages .message {

}

.newMessage {
  grid-area: n;
}